import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import StepLabel from "@mui/material/StepLabel";

const dispatchedEvents = [];

const SrsMuiStepLabel = function (props, ref) {
  return (
    <StepLabel
      active={props.active}
      completed={props.completed}
      disabled={props.disabled}
      error={props.error}
      icon={props.icon}
      optional={props.optional}
      orientation={props.orientation}
      theme={props.muiTheme}
    >
      {props.content}
    </StepLabel>
  );
};

const styles = `
  /* Custom styles for the MuiStepLabel component */
`;

const props = [
  {
    name: "content",
    defaultValue: "",
    type: "string",
  },
  {
    name: "active",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "completed",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "error",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "icon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "optional",
    defaultValue: null,
    type: "string",
  },
  {
    name: "orientation",
    defaultValue: "horizontal",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to StepLabel if needed
];

const slots = [];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiStepLabel),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
